import React, { Component } from 'react';
import './Board.css';
import axios from 'axios';
import Issue from '../components/Issue';
var Loader = require('halogen/PulseLoader');
class Board extends Component {

    constructor(props) {
        super(props);

        this.state = {
            isLoaded: false,
            id: this.props.match.params.boardId,
            Rid: this.props.match.params.sprintId,
            blockers:{},
            issues: {},
            details: '',
            days: 0
        };
    }

    componentDidMount() {
        axios.get('http://erp.dev.adoreme.com:5004?id=' + this.state.id+'&Rid='+ this.state.Rid).then(res => {

            let allBlockers = res.data.issues.filter((issue) => issue.fields.priority.name === 'Blocker');
            let theRest =  res.data.issues.filter((issue) => issue.fields.priority.name !== 'Blocker');

            let blockers = {
                inProgress: allBlockers.filter((issue) => issue.fields.status.name === 'In Progress'),
                pipeline: allBlockers.filter((issue) => issue.fields.status.name === 'To Do'),
                completed: allBlockers.filter((issue) => issue.fields.status.name === 'Done')
            };

            let issues = {
                inProgress: theRest.filter((issue) => issue.fields.status.name === 'In Progress'),
                pipeline: theRest.filter((issue) => issue.fields.status.name === 'To Do'),
                completed: theRest.filter((issue) => issue.fields.status.name === 'Done')
            };

            this.setState({
                isLoaded: true,
                blockers: blockers,
                issues: issues,
                details: res.data.name,
                days: this.nextSprint(res.data.endDate)
            });
        });
    }

    //we dont need this because jira gives us the remaining days
    nextSprint(sprintEndDate) {
        let oneDay = 24*60*60*1000;
        //the day we are now
        var now = new Date();
        let endDate = new Date(sprintEndDate);
        // the difference between now and the end of sprint
        return Math.round(Math.abs((endDate.getTime() - now.getTime())/(oneDay)));

    }
    spellDays(days) {
        let x = days === 1 ? 'day' : 'days';
        return x;
    }
    render() {
        if (!this.state.isLoaded) {
            return <Loader className="spinner" color="#26A65B" size="32px" margin="4px"/>;
        }
        else {
            return (
                <div className="Board">
                    <div className="Board-header">
                        <h1>Board: { this.state.details } - {this.state.days} { this.spellDays(this.state.days) } remaining</h1>
                    </div>
                    <div className="sprint-board">
                        <div className="col pipeline">
                            <h2 className="uppercase">In Pipeline({ this.state.issues['pipeline'].length + this.state.blockers['pipeline'].length  }):</h2>
                            <div className="pipe">
                                {this.state.blockers['pipeline'].map(issue =>
                                    <Issue key={ issue.id } id={ issue.key } info={ issue.fields }/>
                                )}
                                {this.state.issues['pipeline'].map(issue =>
                                    <Issue key={ issue.id } id={ issue.key } info={ issue.fields }/>
                                )}
                            </div>
                        </div>
                        <div className="col in-progress">
                            <h2 className="uppercase">In progress({ this.state.issues['inProgress'].length +  this.state.blockers['inProgress'].length }):</h2>
                            <div className="pipe">
                                {this.state.blockers['inProgress'].map(issue =>
                                    <Issue key={ issue.id } id={ issue.key } info={ issue.fields }/>
                                )}
                                {this.state.issues['inProgress'].map(issue =>
                                    <Issue key={ issue.id } id={ issue.key } info={ issue.fields }/>
                                )}
                            </div>
                        </div>
                        <div className="col done">
                            <h2 className="uppercase">Done({ this.state.issues['completed'].length + this.state.blockers['completed'].length }):</h2>
                            <div className="pipe">
                                {this.state.blockers['completed'].map(issue =>
                                    <Issue key={ issue.id } id={ issue.key } info={ issue.fields }/>
                                )}
                                {this.state.issues['completed'].map(issue =>
                                    <Issue key={ issue.id } id={ issue.key } info={ issue.fields }/>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            );
        }

    }
}

export default Board;



